html {
  background-color: #FFFFFF;
}
.font-bold {
  font-weight: $bold;
}

.font-medium {
  font-weight: $medium;
}
.font-thin {
  font-weight: $thin;
}
.font-regular {
  font-weight: $regular;
}
.font-light {
  font-weight: $light;
}

.main .container:first-of-type {
  background: url('../images/svg/graywave-background.svg');
  background-repeat: repeat-x;
  background-position: bottom left;
  background-size: auto auto;
}



header {
  background-color: #ffffff;
}

.header-content {
  display: flex;
  align-items: center;
}

.nav {
  margin-left: auto;
}

.nav-ul {
  list-style-type: none;
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  display: flex;
}

li a {
  display: block;
  color: black;
  text-align: center;
  text-decoration: none;
  margin: 54px 18px;
}

a[type="button"] {
  background-color: #AF8FEF;
  color: white;
  padding: 13.5px 32px;
  margin-top: 40.5px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.features-title{
  font-style: normal;
  font-weight: normal;
  font-size: 72px;
  line-height: 78px;
  color: #112D57;
  padding:0;
  margin: 0;
}

.features-text {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 38px;
  color: #5C5C5C;
  margin-top: 20px;
  white-space: pre;
}

.features{
  margin: 0;
  display: flex;
}

.features-img-laptop {
  margin-left: auto;
}

.features-img-mobile {
  display: none;
}

.features a {
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 20px;
  text-decoration: none;
  margin-top: 36px;
  padding: 23px 51px;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}

em {
  font-style: normal;
  color: #F79F77;
}

section.elaboration {
  margin-bottom: 120px;
}

.elaboration-wrapper {
  display: flex;
  justify-content: space-between;
}

h2{
  text-align: center;
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 130%;
}

.elaboration-content {
  background: #FFFFFF;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  margin-top: 32px;
  max-width: 269px;
  min-height: 234px;
}

.elaboration-title {
  margin: 21px 0 0 40px;
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 38px;
  color: #FF9D56;
  margin-bottom: 0;
}

.elaboration-text {
  margin: 8px 0 0 40px;
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #112D57;
}

.elaboration-content img {
  margin: 26px 43px 0 43px;
}

.whythis {
  text-align: center;
  margin-bottom: 140px;
}

.whythis-wrapper {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 32px;
}

.whythis-content {
  background: #FFFFFF;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  width: 365px;
  height: 202px;
}

.whythis-content h3 {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: #FF9D56;
  margin: 43px 0 16px 0;
}

.why-this-content p {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 150%;
  text-align: center; 
  letter-spacing: -0.04em;
  color: #112D57;

}

.creator{
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
}

.creator-about {
  padding-left: 61px;
}

.creator-position{
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 130%;
  color: #112D57;
  text-align: start;
  margin-bottom: 8px;
}

.creator-name {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 500;
  font-size: 34px;
  line-height: 130%;
  color: #FF9D56;
  margin: 0;
  padding: 0;
}

.creator-about-list {
  list-style: none;
  padding: 24px 0 0 22px;
  margin: 0;
}

.creator-about-list li::before {
  content: "\2022"; 
  color:#AF8FEF; 
  font-weight: bold; 
  display: inline-block; 
  width: 1em;
  margin-left: -1em; 
}

.creator-about ul li {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 170%;
  color: #112D57;
}

.creator-img-laptop {
  margin-right: 65px;
}

.creator-img-mobile {
  display: none;
}

.diploms {
  display: flex;
  justify-content: center;
  margin-top: 60px;
}

.diplom1, .diplom2, .diplom3 {
  margin-right: 24px;
}

.steps {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.steps-img {
  margin-right: 24px;
}

.steps-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.step-img {
  margin-right: 45px;
}

.steps h3 {
  margin: 0;
  padding: 0;
}

.step-text {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  white-space: pre;
  display: flex;
  flex-direction: column;
}

.step-first h3{
  font-family: Cera Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 170%;
  color: #F5DC53;
}

.step-second h3 {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 170%;
  color: #FF9D56;
}

.step-third h3 {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 130%;
  color: #AF8FEF;
}

.step-first {
  background: url("../images/svg/first.svg") no-repeat left center;
}

.step-second{
  background: url("../images/svg/second.svg") no-repeat left center;
}

.step-third {
  background: url("../images/svg/third.svg") no-repeat left center;
}

.step-first, .step-second, .step-third {
  display: flex;
  flex-direction: column;
  padding-left: 55px;
  margin-bottom: 60px;
}

.example-videos {
  display: flex;
  justify-content: center;
}

.example-videos video {
  margin: 32px 10px 0 10px;
}

.cost {
  margin-top: 120px;
}

.cost-title {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 130%;
  text-align: center;
  color: #112D57;
}

.cost-wrapper {
  display: flex;
  justify-content: center; 
}

.cost-months {
  background: #FFFFFF;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  width: 269px;
  min-height: 271px;
  margin: 32px 20px 0 20px;
}

.cost-wrapper-title {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 38px;
  text-align: center;
  color: #AF8FEF;
}

.cost-wrapper-text {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #112D57;
}

.cost-wrapper-price {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 900;
  font-size: 48px;
  line-height: 38px;
  text-align: center;
  color: #FF9D56;
}

.cost .cost-button {
  text-align:center
}

.cost > .cost-button > .demo-button {
  background: #AF8FEF;
  border-radius: 15px;
  text-decoration: none;
  font-family: Cera Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 20px;
  color: #FFFFFF;
  padding: 23px 65px;
}

.feedback {
  margin-bottom: 80px;
}

.feedback-title {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 130%;
  text-align: center;
  color: #112D57;
}

.feedback-wrapper {
  display: flex;
  justify-content: space-between;
}

.feedback-wrapper-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.feedback-wrapper-text {
  position: relative;
}

.feedback-text {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 150%;
  text-align: center;
  font-feature-settings: 'salt' on, 'liga' off;
  color: #112D57;
  background: #FFFFFF;
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  padding: 24px 40px;
  margin-right: 25px;
  min-height: 180px;
}

.feedback-wrapper-text::after {
  content: "";
  background-image: url("../images/pointer.png");
  position: absolute;
  width: 25px;
  height: 13px;
  bottom: 1px;
  left: 46%;
}

.feedback-authors-photo {
  margin-top: 5px;
}

.demo-video {
  display: flex;
  justify-content: center;
  margin-bottom: 140px;
}

video {
  width: 588px;
  height: 359px;
}

.getdemo-title {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 43px;
  text-align: center;
  font-feature-settings: 'liga' off;
  color: #112D57;
}

.getdemo-text {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  font-feature-settings: 'salt' on, 'liga' off;
  color: #112D57;
  text-align: center;
}


.social {
  display: flex;
  justify-content: center;
  padding-bottom: 113px;
  background: url("../images/svg/grayerwave-background.svg") no-repeat bottom;
}

.social-item {
  display: flex;
  align-items: center;
  margin-right: 33px;
  margin-top: 35px;
}

.telegram-img, .whattsapp-img, .messenger-img {
  padding: 12px;
}

.social-text {
  font-family: Cera Pro;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 34px;
  letter-spacing: -0.04em;
  color: #000000;
}


@include mobile {
  
  .container {
    padding-left: 18px;
    padding-right: 18px;
    margin-left: auto;
    margin-right: auto;
    max-width: 768px;
    width: 100%;
  }

  .header-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
  
  .header-logo {
    max-width: 92px;
    min-height: 51px;
    margin-top: 22px;
  }

  .nav {
    display: none;
  }

  .features {
    display:contents ;
  }

  .features article {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .features-title {
    font-family: Cera Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 78px;
    text-align: center;
  }

  .features-text {
    font-family: Cera Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #5C5C5C;
    white-space: normal;
    margin-top: 2px;
    margin-bottom: 20px;
  }

  .features-img-laptop {
    display: none;
  }

  .features-img-mobile {
    display: unset;
  }

  .main .container:first-of-type {
    background: url("../images/svg/graywave-background-mobile.svg") no-repeat bottom;
  }

  .features article a {
    font-family: Cera Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 20px;

    color: #FFFFFF;
    white-space: pre;
    padding: 18px 40px;
  }

  h2.elaboration {
    font-family: Cera Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 130%;

    text-align: center;

    color: #112D57;
  }

  .elaboration-wrapper {
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 24px;
  }

  .elaboration-content {
    max-width: 164px;
    min-height: 184px;
    margin-top: 11px;
  }

  .elaboration-content img {
    margin: 24px auto auto 24px
  }

  .elaboration-title {
    font-size: 18px;
    line-height: 23px;
    margin: 13px 27px 0 22px; 
  }

  .elaboration-text {
    font-family: Cera Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 130%;
    color: #112D57;
    margin: 9px 16px 6px 22px; 
    white-space: normal;
  }
  
  .whythis-title {
    font-size: 24px;
    line-height: 24px;
  }

  .whythis-content h3 {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
  }

  .whythis-content p {
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
  }

  .whythis-wrapper {
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
  }

  .whythis-content {
    height: unset;
    margin-top: 16px;
  }

  .creator-about {
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 0;
  }

  .creator-img-laptop {
    display: none;
  }

  .creator-img-mobile {
    display: unset;
    text-align: center;
    
  }

  .creator-img-mobile img {
    width: 339px;
    height: 354px;
  }

  .creator-position {
    font-size: 24px;
    line-height: 130%;
    text-align: center;
  }

  .creator-name {
    font-size: 34px;
    line-height: 130%;
    text-align: center;
  }

  .diploms {
    flex-wrap: wrap;
    align-items: center;
    margin-top: 80px;
  }

  .steps h3 {
    font-size: 24px;
    line-height: 130%;
  }

  .steps-wrapper {
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .steps-img {
    display: none;
  }
  
  .step-text {
    font-size: 12px;
    line-height: 170%;
    letter-spacing: -0.04em;
    white-space: normal;
  }
  
  .step-first h3{
    font-size: 24px;
    line-height: 130%;
  }
  
  .step-second h3 {
    font-size: 24px;
    line-height: 130%;
  }
  
  .step-third h3 {
    font-size: 24px;
    line-height: 130%;
  }

  .example-title {
    font-size: 24px;
    line-height: 130%;
  }

  .example-videos {
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;
  }

  .example-videos video {
    width: 339px;
    height: 216px;
    margin: auto;
    margin-top: 13px;
  }

  .cost {
    margin-top: 95px;
  }

  .cost-title {
    font-size: 24px;
    line-height: 130%;
  }

  .cost-wrapper {
    flex-wrap: wrap;
  }

  .feedback-wrapper {
    flex-wrap: wrap;
  }

  .feedback-title {
    font-size: 24px;
    line-height: 130%;
  }

  .feedback-text {
    font-size: 14px;
    line-height: 150%;

    text-align: center;
    font-feature-settings: 'salt' on, 'liga' off;
  }

  .demo-video  {
    display: none;
  }

  .social {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url("../images/svg/grayerwave-background-mobile.svg") no-repeat bottom;
    padding: 0;
  }

  .social-text {
    font-size: 20px;
    line-height: 170%;
  }

  .social-item {
    margin-top: 0px;
    padding: 0;
  }
}

